import React from 'react';
import './Style.css';
import Navbar from '../components/Navbar/NavbarWhite';
import { ReactComponent as NameLogo } from '../imgs/name-logo.svg';
import { ReactComponent as Facebook } from '../imgs/facebook-logo.svg';
import { ReactComponent as LinkedIn } from '../imgs/linkedin-logo.svg';
import { ReactComponent as GitHub } from '../imgs/github-logo.svg';
import { VscMail, VscCallOutgoing } from 'react-icons/vsc';
import { NavLink } from 'react-router-dom';

function home() {
    return (
        <div className="fill-window">
            <div class="homeContainer">
                <Navbar />
                <div className="body">
                    <div>
                        <NameLogo className="nameLogo"/>
                        <div className="flexContainer marginTop50 marginBottom10">
                            <VscCallOutgoing className="homeIcon" />
                            <p class="fontSize16 fontColorGray fontBody">(305)-323-0054</p>                            
                        </div>
                        <div className="flexContainer">
                            <VscMail className="homeIcon" />
                            <a href="mailto:mcgreen417@gmail.com" class="fontSize16 fontColorGray fontUnderline fontBody">mcgreen417@gmail.com</a>
                        </div>
                        <div className="marginTop50">
                            <p class="fontSize20 fontColorGray fontBold fontBody">Front-End and UI/UX<br />Developer</p>
                        </div>
                        <div className="marginTop50">
                            <NavLink to="/projects" className="button fontSize24 fontColorGray fontButton">VIEW PROJECTS</NavLink>
                        </div>
                    </div>
                    <div className="flexContainerColumn">
                        <a href="https://www.facebook.com/mcgreen417/"><Facebook className="socialIcons socialIconsFaceBookColor" /></a>
                        <a href="https://www.linkedin.com/in/michaela-green/"><LinkedIn className="socialIcons" /></a>
                        <a href="https://github.com/mcgreen417"><GitHub className="socialIcons" /></a>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default home;